import React from 'react'
import './Filter.scss'

import FilterTool from '../../Assets/home/filter_tool.svg'

function Filter(props){
  
  const filters = props.arr.map((el, index) => {
    return(
      <button key={index} className={el.active ? 'active filter' : 'filter'}>{el.title}</button>
    )
  })

  return(
    <div className="filters_wrapper">
      <div className="filters">
        {filters}
      </div>
      <button className="filter charity">Charity</button>
      <button className="filter">
        <img src={FilterTool} alt="filter tool"/>
        Filters
        </button>
    </div>

  )
}

export default Filter;